@import "../../styles/variables.scss";

.heroContainer {
  margin-top: 10rem;
  word-break: break-all;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  h2 {
    font-size: 1.5rem;
    color: $primary-color;
  }
}

.customUpload {
  color: $primary-color;
  font-size: 1.5rem;
  background-color: $secondary-color;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
  border: none;
  &:hover {
    background-color: #540505;
  }
}
